<template>
  <div style="padding:30px">
    <div id="top1">
      <p>
        <label>订单号</label>
        <el-input placeholder="订单号"
                  v-model="orderNo"
                  clearable>
        </el-input>
      </p>
      <p>
        <label>收货人</label>
        <el-input placeholder="收货人"
                  v-model="consignee"
                  clearable>
        </el-input>
      </p>
      <p>
        <label>手机号</label>
        <el-input placeholder="手机号"
                  v-model="phone"
                  clearable>
        </el-input>
      </p>
      <p>
        <label>订单状态</label>
        <el-select v-model="value"
                   clearable
                   placeholder="订单状态">
          <el-option v-for="item in options"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>

      </p>
    </div>
    <!-- 时间 -->
    <div id="top2_div">
      <div class="block">
        <span class="demonstration">选择时间</span>
        <el-date-picker v-model="value1"
                        type="datetimerange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
        </el-date-picker>
        <el-button type="primary"
                   @click="clickQuery"
                   style="margin-left:15px">
          查询
        </el-button>
      </div>
    </div>

    <div id="top3">
      <el-table :data="tableData"
                v-loading="loading"
                height="550"
                border
                style="width: 100%">
        <el-table-column fixed
                         prop="orderNo"
                         label="订单号"
                         width="150">
        </el-table-column>
        <el-table-column prop="orderTime"
                         label="下单时间"
                         width="150">
        </el-table-column>
        <el-table-column prop="phone"
                         label="手机号"
                         width="150">
        </el-table-column>
        <el-table-column prop="consignee"
                         label="收货人"
                         width="150">
        </el-table-column>
        <el-table-column prop="deliverAddress"
                         label="配送地址"
                         width="250">
        </el-table-column>
        <el-table-column prop="deliveryTime"
                         label="送达时间"
                         width="250">
        </el-table-column>

        <el-table-column prop="remarks"
                         label="用户备注"
                         width="150">
        </el-table-column>
        <el-table-column prop="orderAmount"
                         label="订单金额"
                         width="150">
        </el-table-column>
        <el-table-column prop="orderState"
                         label="订单状态"
                         width="100">
        </el-table-column>
        <el-table-column fixed="right"
                         label="操作"
                         width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)"
                       type="text"
                       size="small">查看</el-button>
            <el-button type="text"
                       size="small"
                       @click="clickedit(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 翻页 -->
    <div class="block"
         style="margin: 10px auto;">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="[5, 10, 15]"
                     :page-size="pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </div>
    <!-- 订单修改 -->
    <el-dialog title="订单修改"
               :visible.sync="dialogTableVisible">
      <!-- 订单号 -->
      <el-input v-model="newOrderNo"
                clearable>
      </el-input>
      <!-- 下单时间 -->
      <el-date-picker v-model="newOrderTime"
                      type="datetime"
                      placeholder="选择日期时间"
                      default-time="12:00:00">
      </el-date-picker>
      <!-- 手机号 -->
      <el-input v-model="newPhone"
                clearable
                type="number">
      </el-input>

      <!-- 收货人 -->
      <el-input v-model="newConsignee"
                clearable>
      </el-input>

      <!-- 配送地址 -->
      <el-input v-model="newDeliverAddress"
                clearable>
      </el-input>

      <!-- 送达时间 -->
      <el-date-picker v-model="newDeliveryTime"
                      type="datetime"
                      placeholder="选择日期时间"
                      default-time="12:00:00">
      </el-date-picker>
      <!-- 用户备注 -->
      <el-input type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                v-model="newRemarks">
      </el-input>
      <!-- 订单金额 -->
      <el-input v-model="newOrderAmount"
                clearable>
      </el-input>
      <!-- 订单状态 -->
      <el-select v-model="value2">
        <el-option v-for="item in options2"
                   :key="item.value2"
                   :value="item.value2">
        </el-option>
      </el-select>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="Edit_off">取 消</el-button>
        <el-button type="primary"
                   @click="Edit_No">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 订单详情查看 -->
    <el-dialog title="收货地址"
               :visible.sync="dialogFormVisible2">
      <p>
        <label>订单号</label>
        {{newOrderNo}}</p>
      <p>
        <label>下单时间</label>
        {{newOrderTime}}</p>
      <p>
        <label>联系电话</label>
        {{newPhone}}</p>
      <p>
        <label>收货人</label>
        {{newConsignee}}</p>
      <p>
        <label>送货地址</label>
        {{newDeliverAddress}}</p>
      <p>
        <label>送达时间</label>
        {{newDeliveryTime}}</p>
      <p>
        <label>备注</label>
        {{newRemarks}}</p>
      <p>
        <label>订单金额</label>
        {{newOrderAmount}}</p>
      <p>
        <label>订单状态</label>
        {{value2}}</p>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false">取 消</el-button>
        <el-button type="primary"
                   @click="dialogFormVisible2 = false">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// orderedit
import { orderlist, ordersearch, orderdetail, orderedit } from '@/API/apis'//获取账号列表
// getchtime2 getchtime3
import { getchtime2 } from '@/utils/gettime'//时间
export default {
  created () {
    this.Orderlist()
  },
  data () {
    return {
      total: 0,
      //页数
      currentPage: 1,
      pageSize: 5,//每页条数
      orderNo: '',//订单号
      consignee: '',//收货人
      phone: '',//手机号
      options: [
        {
          value: '已完成',
        },
        {
          value: '已受理',
        },
        {
          value: '派送中',
        }
      ],//订单状态集合
      options2: [{
        value2: '已完成',
      },
      {
        value2: '已受理',
      },
      {
        value2: '派送中',
      }],
      value: '',//修改的订单状态
      value2: '',//修改后的订单状态
      // 加载
      loading: true,
      //选择时间
      value1: ["2019-10-05 00:00:00", "2020-10-10 00:00:00"],
      //表格数据
      tableData: [],
      table_search: [],
      nval: [],
      //查询成功是否显示
      dialogTableVisible: false,//模态框
      dialogFormVisible2: false,//查看模态框
      //修改后
      newOrderNo: "",
      newOrderTime: "",
      newPhone: "",
      newConsignee: "",
      newDeliverAddress: "",
      newDeliveryTime: "",
      newRemarks: "",
      newOrderAmount: "",
    }
  },
  methods: {
    // 查询订单列表
    Orderlist () {
      this.loading = true,
        orderlist(this.currentPage, this.pageSize).then(res => {
          this.orderState = res.data.orderState
          this.total = res.data.total
          this.arr = res.data.data
          for (const obj of this.arr) {
            obj.orderTime = getchtime2(obj.orderTime)//下单时间
            obj.deliveryTime = getchtime2(obj.deliveryTime)//送达时间
          }
          this.tableData = this.arr
          this.loading = false
        })
    },
    // 翻页部分
    handleSizeChange (pageSize) {
      this.pageSize = pageSize
      this.Orderlist()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.Orderlist()
    },
    //顶部查询部分
    clickQuery () {
      for (const objdata of this.value1) {
        this.objdata = getchtime2(objdata)
        this.nval.push(this.objdata)
      }
      ordersearch(this.currentPage, this.pageSize, this.orderNo, this.consignee, this.phone, this.value, this.nval).then(res => {
        this.arr2 = res.data.data
        for (const obj of this.arr2) {
          obj.orderTime = getchtime2(obj.orderTime)//下单时间
          obj.deliveryTime = getchtime2(obj.deliveryTime)//送达时间
        }
        this.tableData = this.arr2
        console.log(res);
        if (res.data.total == 0) {
          this.$message({
            message: '请输入完整资料',
            type: 'warning'
          });
          this.dialogVisible = true
        }
      })
    },
    // 详情查看部分
    // 改
    clickedit (row) {
      this.dialogTableVisible = true
      this.id = row.id
      this.value2 = row.orderState
      this.newOrderNo = row.orderNo//订单号
      this.newOrderTime = row.orderTime//下单时间
      this.newPhone = row.phone//手机号
      this.newConsignee = row.consignee//
      this.newDeliverAddress = row.deliverAddress
      this.newDeliveryTime = row.deliveryTime
      this.newRemarks = row.remarks
      this.newOrderAmount = row.orderAmount
      this.value2 = row.orderState
    },
    // 取消修改
    Edit_off () {
      this.dialogTableVisible = false
      this.$message('取消修改');
    },
    // 确认修改
    Edit_No () {
      this.dialogTableVisible = false
      //发送修改后的内容
      orderedit(this.id, this.newOrderNo, this.newOrderTime, this.newPhone, this.newConsignee, this.newDeliverAddress, this.newDeliveryTime, this.newRemarks, this.newOrderAmount, this.value2).then(res => {
        if (res.data.code == 0) {
          this.Orderlist()
          this.$message({
            message: '修改订单信息成功!!!',
            type: 'success'
          });
        }
      })
    },
    // 查看订单详情
    handleClick (row) {
      this.value2 = row.orderState
      this.newOrderNo = row.orderNo//订单号
      this.newOrderTime = row.orderTime//下单时间
      this.newPhone = row.phone//手机号
      this.newConsignee = row.consignee//
      this.newDeliverAddress = row.deliverAddress
      this.newDeliveryTime = row.deliveryTime
      this.newRemarks = row.remarks
      this.newOrderAmount = row.orderAmount
      this.value2 = row.orderState
      this.dialogFormVisible2 = true,//查看模态框
        orderdetail(row.id).then(res => {
          console.log(res);
        })
    },
  }
}
</script>

<style lang="less" scoped>
#top1 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  .el-input,
  .el-select {
    width: 217px;
    margin-left: 15px;
  }
}
#top2_div {
  margin-bottom: 30px;
}
.top3 {
  .block {
    min-height: 300px;
    max-height: 600px;
    width: 400px;
  }
}
</style>